<template>
  <div>

    <div class="fw-b" :class="`fs-${fs_type}-36`">{{ pdf_title }}</div>
    <div :class="`fs-${fs_type}-22`" class="pt-1 pb-2">
      {{ pdf_addr }}
    </div>
    <div :class="`fs-${fs_type}-20`">
      {{ $t('PDF_MSG.phoned') }}：{{ pdf_contact_phoned }}<!--&nbsp;&nbsp;{{ $t('PDF_MSG.fax') }}：{{ pdf_contact_fax }}-->
    </div>
    <div :class="`fs-${fs_type}-20`">
      {{ $t('PDF_MSG.email') }}：{{ pdf_contact_mail }}
    </div>
    <div class="mt-2 fw-b" :class="`fs-${fs_type}-30`">
      <slot></slot>
    </div>
    
  </div>
</template>

<script>
export default {
  name: 'PdfHeader',
  props: {
    fs_type: {
      type: String,
      default: 'em'
    }
  },
  computed: {
    pdf_addr() {
      let res = 'Office: Room A20, 8/F, Lee King Industrial Building, 12 Ng Fong Street, San Po Kong, Kowloon, Hong Kong'
      const lan = this.$i18n.locale + ''
      if (lan == 'zh-HK') {
        res = '辦公室 : 香港九龍新蒲崗五芳街12號利景工業大廈8樓A20室'
      }
      return res
    }
  },
  mounted() {
    console.log()
  },
  data() {
    return {
      pdf_title: 'Lumicrest International Limited',
      pdf_sub: 'Office: Room A20, 8/F, Lee King Industrial Building, 12 Ng Fong Street, San Po Kong, Kowloon, Hong Kong', // 'Office: Rm A12, 8/F Lee King Industrial Building 12 Ng Fong Street, San Po Kong, Kowloon, Hong',
      pdf_contact_phoned: '27776080', // '5488 1979 5424 4119',
      pdf_contact_fax: 'cancel', // '2777 0760',
      pdf_contact_mail: 'contact@lumicrest.com.hk'
    }
  }
}
</script>


<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/pdf.sass"
  
</style>


